import React, { useContext, useEffect, useState } from 'react';
import { CarouselProvider, Slider, Slide, Dot, CarouselContext  } from 'pure-react-carousel';
import 'pure-react-carousel/dist/react-carousel.es.css';
import {makeStyles} from "@material-ui/core/styles";
import styled from 'styled-components';

const useStyles = makeStyles((theme) => ({
    root: {
        position: 'relative',
    },
    dot: {
        background: '#101014',
        borderRadius: '75px',
        border: '1px solid #101014',
        //color: 'skyblue'
        margin: '1px',
        maxWidth: '1px',
        opacity: '30%',
    },
    dotSelected: {
        borderRadius: '75px',
        background: '#101014',
        border: '1px solid #101014',
    },
    dotBox: {
        textAlign: 'center',
        position: 'absolute',
        bottom: 0,
        left: 0,
        right: 0,
    },
    icon:{
        fontSize: '1rem',
        color: 'white'
    },
    arrowButton:{
        position: 'absolute',
        borderRadius: '50%',
        backgroundColor: 'rgba( 0, 0, 0, 0.2 )',
        height: '20px',
        width: '20px',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        border: 'rgba( 255, 255, 255, 0 )',
        top: '50%',
        transform: 'translateY(-50%)',
        zIndex: 3,
    },
    nextButton: {
        right: 5,
    },
    backButton: {
        left: 5,
    }
}));
const SlideImage=styled.div`
    width: 100%;
    height: 100%;
    background-image: url(${props=> props.src});
    background-position: center;
    background-repeat: no-repeat;
    background-size : cover;
`;


function ComponentsUsingContext({imgUrlList, classes}) {
    const carouselContext = useContext(CarouselContext);
    const [currentSlide, setCurrentSlide] = useState(carouselContext.state.currentSlide);
    useEffect(()=> {
        function onChange() {
            setCurrentSlide(carouselContext.state.currentSlide);
        }
        carouselContext.subscribe(onChange);
        return () => carouselContext.unsubscribe(onChange);
    }, [carouselContext]);
    const dotComponents=imgUrlList.map((image, index)=>(
        index===currentSlide ? (
            <Dot key={index} className={classes.dotSelected} slide={index}/>
        ) : (
            <Dot key={index} className={classes.dot} slide={index}/>
        )
    ));

    return (
        <>
            <div className={classes.dotBox}>{dotComponents}</div>
        </>
    )
}
export default function Carousel({imgUrlList}) {
    const url='http://localhost:5000';
    const classes = useStyles();
    const count=imgUrlList.length;
    return (
        <div className={classes.root}>
            <CarouselProvider
                naturalSlideWidth={100}
                naturalSlideHeight={70}
                totalSlides={count}
                dragEnabled={false}
                isPlaying={true}
            >
                <Slider>
                    {
                        imgUrlList.map((image, index)=>(
                            <Slide index={index} key={index}><SlideImage src={url+image}/></Slide>
                        ))
                    }
                </Slider>

                {imgUrlList.length>1 && <ComponentsUsingContext imgUrlList={imgUrlList} classes={classes}/>}
            </CarouselProvider>
        </div>
    );
}