import React from 'react';
import styled from "styled-components";


/*  */
const HomeTemplateBlock=styled.div`
 
   
    margin-top:4rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    
    background: #313233; 
   

`;

export default function HomeTemplate({children}){
    return (
        <>

            <HomeTemplateBlock>
                {children}
            </HomeTemplateBlock>
        </>
    );
};
